import React, { useState, useEffect, useMemo } from "react";
import { Modal, Form, Divider, Button, message, Select } from "antd";
import { FormInput, FlexEnd, FormSelect } from "..";
import { useGetData, usePostData, useUpdateData } from "../../Hooks";
import { setInput } from "../../Utils/myFormat";
import { FiNavigation } from "react-icons/fi";

const { Option } = Select;

const ModalCreateUpdateCluster = ({
  visible,
  onCancel,
  setUpdateData,
  data,
  updating,
}) => {
  const { data: dataArea, loading: loadingArea } = useGetData("area");
  const [clusterId, setClusterId] = useState("");
  const [clusterName, setClusterName] = useState("");
  const [areaId, setAreaId] = useState("");
  const [status, setStatus] = useState("");

  const areaName = useMemo(() => {
    if (!areaId) {
      return "";
    }

    const area = [...dataArea].filter((item) => item.id === areaId);

    return area[0].name;
  }, [dataArea, areaId]);

  const newArea = useMemo(() => dataArea, [dataArea]);

  const resetState = () => {
    setClusterId("");
    setClusterName("");
    setAreaId("");
    setStatus("");
    form.resetFields();
  };

  const [form] = Form.useForm();

  const [dataPost, setDataPost] = useState({
    url: "",
    data: {},
    trigger: 0,
  });
  const [dataUpdate, setDataUpdate] = useState({
    url: "",
    data: {},
    trigger: 0,
  });

  const { onSuccess: successPost, loading: loadingPost } = usePostData(
    dataPost.url,
    dataPost.data,
    dataPost.trigger
  );
  const { onSuccess: successUpdate, loading: loadingUpdate } = useUpdateData(
    dataUpdate.url,
    dataUpdate.data,
    dataUpdate.trigger
  );

  const handlePostCluster = () => {
    if (!clusterName || !areaId || !areaName || status === "") {
      message.warn("Form tidak boleh kosong");

      return null;
    }

    const dataPost = {
      area_name: areaName,
      area_id: areaId,
      name: clusterName,
      status: status,
    };
    console.log("data post: ", dataPost);

    setDataPost({
      url: "area/create/cluster",
      data: dataPost,
      trigger: new Date().getTime(),
    });
  };

  const handleUpdateCluster = () => {
    if (!clusterId || !clusterName || !areaId || !areaName || status === "") {
      message.warn("Form tidak boleh kosong");

      return null;
    }

    const dataUpdate = {
      area_name: areaName,
      area_id: areaId,
      name: clusterName,
      status: status,
    };
    console.log("data update: ", dataUpdate);

    setDataUpdate({
      url: `area/edit/cluster?cluster_id=${clusterId}`,
      data: dataUpdate,
      trigger: new Date().getTime(),
    });
  };

  useEffect(() => {
    if (successPost.includes("success")) {
      message.success(`Berhasil Create Area ${areaId}`);
      resetState();
      onCancel();
      setUpdateData(Date.now());
    }
    if (successUpdate.includes("success")) {
      message.success(`Berhasil Update Area ${areaId}`);
      resetState();
      onCancel();
      setUpdateData(Date.now());
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [successPost, successUpdate]);

  useEffect(() => {
    if (data.id && updating) {
      setClusterId(data.id);
      setClusterName(data.name);
      setAreaId(data.area_id);
      setStatus(data.status);
      form.setFieldsValue({
        clusterName: data.name,
        areaId: data.area_id,
        status: data.status,
      });
    } else {
      resetState();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data, updating]);

  return (
    <Modal
      title={updating ? "Update Cluster" : "Create Cluster"}
      visible={visible}
      onCancel={onCancel}
      footer={false}
    >
      <Form form={form} name="createsite" autoComplete="off">
        <FormInput
          label="Cluster Name"
          requiredLabel
          name="clusterName"
          required
          message="please input cluster name!"
          placeholder="input cluster name"
          onChange={(e) => setInput(e, setClusterName)}
        />
        <FormSelect
          name="areaId"
          label="Area"
          requiredLabel
          required
          message="Please select area!"
          placeholder="select area"
          allowClear
          showSearch
          optionFilterProp="children"
          filterOption={(input, option) =>
            option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
          }
          onChange={(e) => setAreaId(e)}
          loading={loadingArea}
          disabled={loadingArea}
        >
          {newArea.map((item) => (
            <Option value={item.id} key={item.id}>
              {`[${item.id}] ${item.name}`}
            </Option>
          ))}
        </FormSelect>
        <FormSelect
          name="status"
          label="Status"
          requiredLabel
          required
          message="Please select status!"
          placeholder="select status"
          onChange={(e) => setStatus(e)}
        >
          <Option value={1}>Active</Option>
          <Option value={0}>Inactive</Option>
        </FormSelect>
        <Divider />
        <FlexEnd>
          <Form.Item>
            {updating ? (
              <Button
                type="primary"
                shape="round"
                icon={<FiNavigation style={{ marginRight: "6px" }} />}
                onClick={handleUpdateCluster}
                loading={loadingUpdate}
                htmlType="submit"
              >
                Update Cluster
              </Button>
            ) : (
              <Button
                type="primary"
                shape="round"
                icon={<FiNavigation style={{ marginRight: "6px" }} />}
                onClick={handlePostCluster}
                loading={loadingPost}
                htmlType="submit"
              >
                Create Cluster
              </Button>
            )}
          </Form.Item>
        </FlexEnd>
      </Form>
    </Modal>
  );
};

export default ModalCreateUpdateCluster;
